$style: "sixth-section";
.#{$style} {
    padding: 100px 0;
    &__title {
        max-width: 320px;
        margin-bottom: 100px;
    }
    &__list {
        @include flex(space-around, center, row);
    }
    &__description {
        &--first {
            left: -140px;
            top: -60px;
        }
        &--second {
            left: -138px;
            top: -60px;
        }
        &--third {
            left: -262px;
            top: -75px;
        }
        &--fourth {
            left: -262px;
            top: -75px;
        }
    }
}